  display: block;
  border: 2px solid #fff;
  border-radius: 6px;
}

.Card_Card__D30ru .Card_Front__1-5yC {
  transform: rotateY(90deg);
  transition: all ease-in 0.2s;
  position: absolute;
}

.Card_Flipped__17fW1 .Card_Front__1-5yC {
